import React, {useState, useEffect} from 'react';
import {SocialMedias, AboutContent, Item, ItemGallery} from './utilityComponents';
import {CSSTransition} from "react-transition-group"

import logo from './static/JE_Logo.svg';
import facebook from './static/facebook.svg'
import twitter from './static/twitter.svg'
import github from './static/github-sign.svg'
import instagram from './static/instagram.svg'


import { CurrentItem, GalleryItem } from './item_query';

export const Page1=React.memo(()=>{
    
        return(
            <div id="About" className="background-grade" style={{background: "radial-gradient(circle, rgba(32,32,32,1) 30%, rgb(18, 18, 18) 50%, rgba(0,0,0,1) 100%)"}}>
                <div className="filter" style={{backdropFilter: "blur(110px)"}}>
                    <LeftAbout />
                    <RightAbout />
                </div>
            </div>
        )

})

const LeftAbout=React.memo(()=> {
    const [icon, setIcon] = useState([])
    const [myContent, setMyContent] = useState('')
    const [links, setLinks] = useState([])
    useEffect(()=>{
        content()
    })
    const content = ()=>{
        setIcon([instagram,twitter, facebook,  github])
        setMyContent(` Hi, I am Johndel Encabo, a Filipino Web Developer and UI Designer, I am currently on college, CS is my course. I indepently learning  React JS and Django is the Framework I use on my Projects. 
        I love macro photography, I  always taking of picture of small things
        I am passionate on what I am doing and any problem I encounter I definitly finding my solution. I keep on learning and studying things related to my field, at first planning was my weakness but that weakness became a strangth.`)
        setLinks(["https://www.instagram.com/thejohnds/","https://twitter.com/johndkalang","https://www.facebook.com/johndel.En13/","https://github.com/cooldragon12/"])
    }
        return(
            <div className="left">
                <div className="back-left" style={{backgroundImage: `url(`+logo+`)`}}></div>
                <h2 className="about-title">ABOUT</h2>
                <AboutContent content={myContent} id={"about-desc"}/>
                <div className="social-media-cont">
                    <SocialMedias icons={icon} links={links}/>
                </div>
            </div>
            
        )
    
})
const RightAbout=React.memo(()=>{
        return(
            <div className="right">
                <div className="right-up">
                    <div className="title">
                        <h2>JOHNDEL</h2>
                        <h1>PORTFOLIO</h1>
                    </div>
                </div>
                <div className="right-down">
                    <div className="logo" style={{backgroundImage: "url('"+logo+"')"}}></div>
                </div>
            </div>
            
        )
    
})
export const Page2 = React.memo(()=>{
    const [openItem1, setOpenItem1]=useState(false)
    const [openItem2, setOpenItem2]=useState(false)
    const [galleryItem, setGalleryItem] = useState([])
    const [projectItem, setProjectItem] = useState([])

    useEffect(()=>{
        getQuery()
    })
    const getQuery = ()=>{
        setProjectItem(CurrentItem)
        setGalleryItem(GalleryItem)

    }
    const closeThis1 = () => {
        setOpenItem1(false)
     };  
     const openThis1 = () => {
        setOpenItem1(true);
        setOpenItem2(false);
      
     };
     const closeThis2 = () => {
        setOpenItem2(false)
     };  
     const openThis2 = () => {
        setOpenItem2(true);
        setOpenItem1(false)


     };
    return(
            <div className="second-page">
                <div id="Project" onClick={openItem1 === false?openThis1:closeThis1} className={`left second1 ${openItem1 === true?"openSecond1":""}`}>
                    <div className={`extr-title ${openItem1 === true?"openTitle":"title-cont"}`}>
                        <h1 className={`title-proj`}>PROJECTS</h1>

                    </div>
                    <CSSTransition
                        unmountOnExit
                        in={openItem1}
                        timeout={800}
                        classNames="appear"
                    >
                        <ItemCont selection="proj" dataList={projectItem}/>
                    </CSSTransition>
                </div>
                <div id="Gallery"  onClick={openItem2 === false?openThis2:closeThis2} className={`right second2 ${openItem2 === true?"openSecond2":""}`}>
                    <div className={`extr-title ${openItem2 === true?"openTitle":"title-cont"}`}>
                        <h1 className={`title-gallery`}>GALLERY</h1>
                    </div>
                    <CSSTransition
                        unmountOnExit
                        
                        in={openItem2}
                        timeout={900}
                        classNames="appear"
                    >
                        <ItemCont  selection="gallery" dataList={galleryItem}/>
                    </CSSTransition>

                </div>
            </div>
        )
    
})

        


const ItemCont=React.memo(props=>{
    var selected = props.selection
    const itemlist =props.dataList
    const [listItem, setListItem ] = useState([])
    var style = ""
    switch (selected){
        case "proj":
            style = "item-cont column-one"
            break
        case "gallery":
            style = "item-cont row-gallery"
            break
        default:
            style = "item-cont"
            break

    }
    useEffect(()=>{itemFetcher()});
    const itemFetcher = () =>{
        setListItem(itemlist)
    }
    return (
        
        <div className={style}>
            {
                selected === "proj"?listItem.map((item, index)=>{
                    return <Item key={index} data={item}/>
                }):listItem.map((item, index)=>{
                    return <ItemGallery key={index} data={item}/>
                })
            }
        </div>
    )
})

